'use client'

import Menu from '@/components/menu/Menu'
import Headline from '@/components/ui/Headline'
import ProjectCard from '@/components/portfolio/ProjectCard'

const BASE_PATH = process.env.NEXT_PUBLIC_BASE_PATH || ''

export default function PortfolioPage() {
  return (
    <>
      <Menu />
      <section
        style={{
          backgroundColor: 'black',
          color: 'white',
          minHeight: '100vh',
          paddingTop: '64px',
          paddingInline: '1rem',
        }}
      >
        <Headline lineTop="Selected works" lineBottom="Portfolio" />

        <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', gap: '2rem', marginTop: '3rem' }}>
          <ProjectCard
            videoSrc={`${BASE_PATH}/videos/masterbean.mp4`}
            logoSrc={`${BASE_PATH}/as-logo.svg`}
            projectName="Project One"
            description="This is a sample description for Project One."
          />
          <ProjectCard
            videoSrc={`${BASE_PATH}/videos/masterbean.mp4`}
            logoSrc={`${BASE_PATH}/as-logo.svg`}
            projectName="Project Two"
            description="This is a sample description for Project Two."
          />
        </div>
      </section>
    </>
  )
}
